import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import { gsap } from "gsap";
import { Link } from "react-router-dom";
//import universe from "../assets/images/universe.jpg"

export default function Main() {
  // gsap.to(".box1", {x:650, duration:0.52, data:"about me"})
  // gsap.to(".box2", {x:-650, duration:0.5})
  const circleRef = useRef();
  const circleReff = useRef();
  // const [active, setActive] = useState(true)
  // useEffect(() => {
  //   const timeout = setTimeout(() => remove(), 1000)
  //   return () => clearTimeout(timeout)
  // },[])
  // const remove = () => {
  //   const ani = gsap.to(circleRef.current,{
  //     display:"none"
  //   })
  // }
  useEffect(() => {
    const timeout = setTimeout(() => show2(), 2000);
    return () => clearTimeout(timeout);
  }, [circleRef]);
  useEffect(() => {
    const timeout = setTimeout(() => show1(), 2100);
    return () => clearTimeout(timeout);
  }, [circleReff]);
  const show1 = () => {
    const ani = gsap.to(circleReff.current, {
      x: -750,
      y: -450,
      duration: 0.5,
      ease: "strong.inOut",
    });
    return () => {
      ani.kill();
    };
  };
  const show2 = () => {
    const ani = gsap.to(circleRef.current, {
      x: 900,
      y: 450,
      duration: 0.5,
      ease: "strong.inOut",
    });
    return () => {
      ani.kill();
    };
  };
  return (
    <Contain>
      <div className="box box1" id="box1" ref={circleRef}>
        <Link to="/about">ABOUT ME</Link>
      </div>
      <div className="box box2" ref={circleReff}>
        <Link to="/my">MY PORTFOLIO</Link>
      </div>
    </Contain>
  );
}
const Contain = styled.div`
  @keyframes ball {
    0% {
      opacity: 0.2;
    }
    55% {
      opacity: 0.7;
    }
    to {
      opacity: 0.9;
    }
  }
  background-image: url("../assets/images/universe.jpg");
  width: 800px;
  height: 1300px;
  display: flex;
  flex-direction: column;
  .box {
    animation: ball 1.5s ease-in infinite alternate;
    width: 580px;
    height: 580px;
    border-radius: 2000px;
    color: #ffffff;
    text-align: center;
    align-items: center;
    text-decoration: underline;
    text-underline-position: under;
    line-height: 580px;
    font-size: 60px;
    font-weight: bold;
    border: 7px solid #474747;
  }
  .box1 {
    margin-right: 400px;
    background-image: url("data:image/jpeg;base64,");
    background-repeat: no-repeat;
    background-size: 580px;
    background-position: absolute;
    background-position-x: -5px;
    opacity: 0.7;
    box-shadow: -35px 35px 35px rgba(0, 0, 0, 0.5);
  }

  .box2 {
    opacity: 0.7;
    margin-left: 2350px;
    background-image: url("data:image/jpeg;base64,");
    background-repeat: no-repeat;
    background-size: 855px;
    background-position: absolute;
    background-position-x: -135px;
    background-position-y: -30px;
    box-shadow: 35px 35px 35px rgba(0, 0, 0, 0.5);
  }
`;
